.order {

  &__form {
    display: flex;
    margin-top: 30px;

    @media (max-width: 991px) {
      flex-direction: column;
    }
  }

  &__left {
    @media (min-width: 992px) {
      width: 55%;
    }

    @media (min-width: 1200px) {
      width: 65%;
    }

    .selectric-items {
      li {
        &:first-child {
          display: none;
        }
      }
    }
  }

  &__right {

    @media (min-width: 992px) {
      width: 45%;
      margin-left: 50px;
    }

    @media (min-width: 1200px) {
      width: 35%;
      margin-left: 70px;
    }

    @media (max-width: 991px) {
      margin-top: 30px;
    }
  }

  &__basket {
    margin-top: 50px;
  }

  &__item {
    display: flex;
    border-bottom: 2px solid #F3F1F2;
    padding-bottom: 80px;
    align-items: center;

    @media (max-width: 1200px) {
      flex-direction: column;
    }

    @media (max-width: 991px) {
      padding-bottom: 40px;
      align-items: center;
    }
  }

  &__item + &__item {
    margin-top: 40px;

    @media (min-width: 576px) {
      margin-top: 70px;
    }
  }

  &__img {
    width: 100%;
    max-width: 230px;
    height: 170px;

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &__holder {
    position: relative;
    margin-left: 70px;
    display: flex;
    align-items: flex-end;
    width: 100%;

    @media (max-width: 1200px) {
      margin: 30px 0 0 0;
    }

    @media (max-width: 991px) {
      flex-direction: column;
      align-items: flex-start;
      max-width: 300px;
    }
  }

  &-shoes {
    &__desc {
      font-size: 20px;
      font-weight: 700;
      text-transform: lowercase;

      @media (min-width: 576px) {
        font-size: 30px;
      }

      @media (min-width: 1350px) {
        font-size: 44px;
      }
    }

    &__item {
      display: flex;
      align-items: center;
      margin-top: 30px;
      padding-bottom: 10px;
    }

    &__color {
      display: flex;
      align-items: center;
      font-size: 18px;
      font-weight: 700;

      @media (min-width: 576px) {
        font-size: 20px;
      }

      @media (min-width: 1350px) {
        font-size: 26px;
      }

      span {
        position: relative;
        border-radius: 100%;
        display: block;
        width: 16px;
        height: 16px;
        margin-left: 20px;
        color-adjust: exact;
        @media (min-width: 576px) {
          width: 20px;
          height: 20px;
          margin-left: 35px;
        }

        &::before {
          content: "";
          position: absolute;
          left: 50%;
          top: 50%;
          border-radius: 100%;
          width: 24px;
          height: 24px;
          transform: translate(-50%, -50%);
          border: 2px solid #000;

          @media (min-width: 576px) {
            width: 28px;
            height: 28px;
          }
        }
      }
    }

    &__size {
      margin-left: 30px;
      font-size: 18px;
      font-weight: 700;

      @media (min-width: 576px) {
        font-size: 20px;
        margin-left: 40px;
      }

      @media (min-width: 1350px) {
        font-size: 26px;
      }


      span {
        margin-left: 20px;
        font-size: 20px;
        font-weight: 400;

        @media (min-width: 576px) {
          margin-left: 35px;
        }
      }
    }
  }

  &-seller {
    margin-left: 45px;

    @media (max-width: 991px) {
      margin: 25px 0 0 0;
    }

    &__desc {
      margin-bottom: 20px;
      font-size: 18px;
      font-weight: 700;

      @media (min-width: 576px) {
        font-size: 20px;
      }

      @media (min-width: 1350px) {
        font-size: 26px;
      }
    }

    .selectric {
      width: 280px;

      @media (min-width: 350px) {
        width: 300px;
      }

      @media (min-width: 992px) {
        width: 250px;
      }

      @media (min-width: 1350px) {
        width: 350px;
      }

      .label {
        font-weight: 700;
      }

      &-items {
        li {
          font-weight: 700;
        }
      }
    }
  }

  &__box {
    display: flex;
    align-items: center;
    margin-left: 55px;
    padding-bottom: 10px;

    @media (max-width: 991px) {
      margin: 25px 0 0 0;
      width: 100%;
    }
  }

  &-count {
    display: flex;
    align-items: center;
    font-size: 26px;

    &__minus,
    &__plus {
      display: block;
      width: 25px;
      height: 25px;
      background-repeat: no-repeat;
      background-size: cover;
      cursor: pointer;
      opacity: .4;
      transition: opacity .2s ease-in;

      &:hover {
        opacity: 1
      }
    }

    &__minus {
      background-image: url('../img/minus.svg')
    }

    &__plus {
      background-image: url('../img/plus.svg')
    }

    &__number {
      border: none;
      width: 25px;
      text-align: center;
      margin: 0 5px;
      padding: 0;
      font-size: 26px;
    }

    p {
      font-weight: 700;
    }

    i {
      font-style: normal;
      font-size: 16px;
      margin: auto 0 0 5px
    }
  }

  &__price {
    display: flex;
    margin-left: 50px;

    p {
      font-weight: 700;
    }

    i {
      font-style: normal;
      font-size: 16px;
      margin: auto 0 0 5px
    }
  }

  &__close {
    opacity: .4;
    transition: opacity .2s ease-in;

    @media (min-width: 992px) {
      position: absolute;
      top: 40px;
      right: 0;
    }

    @media (max-width: 991px) {
      margin-left: auto;
    }

    &:hover {
      opacity: 1
    }
  }

  &__bottom {
    margin-top: 35px;

    @media (min-width: 991px) {
      margin-top: 65px;
    }
  }

  &__total {
    font-size: 26px;
    font-weight: 700;
    color: $color-orange;
    text-align: center;

    @media (min-width: 992px) {
      text-align: right;
    }

    span {
      margin-left: 25px;
    }

    i {
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
    }
  }

  &__buttons {
    margin-top: 40px;
    display: flex;
    justify-content: space-between;

    @media (max-width: 991px) {
      flex-direction: column;
      margin-top: 30px;
    }

    .btn {
      text-transform: none;
      height: 60px;
      font-size: 16px;

      @media (min-width: 360px) {
        font-size: 20px;
      }

      @media (min-width: 992px) {
        min-width: 350px;
      }
    }

    .btn + .btn {
      @media (max-width: 991px) {
        margin-top: 15px;
      }
    }
  }

  &-info {
    &__inputs {
      margin-top: 30px;
      display: grid;
      grid-gap: 30px 15px;
      align-items: end;

      @media (min-width: 576px) {
        margin-top: 50px;
        grid-template-columns: repeat(2, 1fr);
      }

      .alert {
        margin-top: 0px;
      }
    }

    &__inputs.col1 {
      grid-template-columns: 1fr;
    }

    &__input.col1 {
      grid-column: 1/-1;
    }
  }

  &-booking {
    margin-top: 40px;

    @media (min-width: 576px) {
      margin-top: 80px;
    }

    @media (max-width: 991px) {
      .order__item {
        flex-direction: row;
      }

      .order__holder {
        flex-direction: row;
        max-width: 100%;
        margin: 0 0 0 30px;
        align-items: flex-end;
      }

      .order-shoes {
        width: 100%;
      }
    }

    @media (max-width: 767px) {
      .order__item {
        flex-direction: column;
      }

      .order__holder {
        max-width: 100%;
        margin: 30px 0 0 0;
      }

      .order-shoes {
        width: 100%;
      }
    }

    @media (max-width: 575px) {
      .order__item {
        align-items: center;
        padding-bottom: 20px;
      }

      .order__holder {
        flex-direction: column;
        max-width: 330px;
      }
    }

    &__title {
      display: flex;
      flex-wrap: wrap;
      font-size: 20px;
      color: $color-orange;
      font-weight: 700;
      margin-bottom: 30px;

      @media (min-width: 576px) {
        font-size: 26px;
      }

      p {
        margin-right: 14px;

        @media (min-width: 576px) {
          margin-right: 20px;
        }

        @media (max-width: 575px) {
          &:not(:last-child) {
            margin-bottom: 5px;
          }
        }
      }
    }

    &__total {
      margin-top: 25px;
      font-size: 26px;
      font-weight: 700;
      display: flex;
      justify-content: space-around;

      @media (min-width: 576px) {
        justify-content: space-between;
      }

      i {
        font-weight: 400;
        font-style: normal;
        font-size: 21px;
      }
    }

    .order {

      &__item {
        padding-bottom: 40px;
      }

      &__img {
        max-width: 100px;
        height: 75px;

        @media (max-width: 1200px) {
          max-width: 170px;
          height: 125px;
        }
      }

      &__right {
        margin-left: 30px;
      }

      &-shoes {

        &__desc {
          @media (min-width: 576px) {
            font-size: 26px;
          }
        }

        &__item {
          margin-top: 20px;
        }

        &__size,
        &__color {
          font-size: 16px;
          font-weight: 400;
        }

        &__color span {
          width: 16px;
          height: 16px;
          margin-left: 20px;

          &::before {
            width: 24px;
            height: 24px;
          }
        }

        &__size {
          margin-left: 20px;

          span {
            margin-left: 20px;
          }
        }
      }

      &__box {
        margin-left: 35px;
        padding-bottom: 9px;
      }

      &__price {
        margin-left: 30px;
        font-size: 26px;
      }
    }
  }

  .selectric {
    border-radius: 100px;
    background: #F3F1F2;
    border: none;
    height: 50px;

    .button {
      background: #F3F1F2;
      height: 50px;

      &::before {
        content: '';
        background-image: url('../img/arrow-down-3.svg');
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        position: absolute;
        top: 50%;
        right: 20px;
        transform: translateY(-50%);
        width: 10px;
        height: 7px;
        transition: all 0.3s ease;
      }

      &:after {
        content: none;
      }
    }

    .label {
      margin-left: 25px;
      color: #4A4A4A;
      font-size: 16px;
      line-height: 50px;
      height: 50px;
    }

    &-open {
      .selectric {
        border-radius: 20px 20px 0 0;

        .button {
          &::before {
            transform: translateY(-50%) rotate(180deg);
          }
        }
      }
    }

    &-items {
      background: #F3F1F2;
      border: none;
      border-radius: 0 0 20px 20px;
      overflow: hidden;
      box-shadow: none;

      ul {
        padding: 0 25px;
      }

      li {
        padding: 20px 0;
        color: #4A4A4A;
        font-size: 16px;
        transition: all 0.1s ease;
        border-top: 1px solid #E8E5E6;

        &:hover {
          background-color: transparent;
          color: $color-orange;
        }

        &.highlighted {
          background-color: transparent;
          color: $color-orange;
        }
      }
    }
  }

  &__title {
    font-size: 18px;
    font-weight: 700;
    text-transform: uppercase;

    @media (min-width: 576px) {
      font-size: 26px;
      margin-right: 20px;
    }
  }

  &__payment {
    margin-top: 35px;

    .selectric-wrapper {
      margin-top: 30px;

      &.order__flex {
        @media (max-width: 575px) {
          margin-top: 60px !important;
        }
      }
    }
  }

  &__flex {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-top: 30px !important;

    @media (max-width: 1200px) {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__desc {
    color: #4A4A4A;
    line-height: 19px;

    @media (max-width: 1200px) {
      margin-top: 10px;
    }
  }

  &__delivery {
    margin-top: 60px;
  }

  &__radio {
    margin-top: 35px;
    display: flex;

    @media (max-width: 575px) {
      flex-direction: column;
    }
  }

  &__label {
    position: relative;
    display: flex;
    cursor: pointer;

    input {
      display: none;

      &:checked + span {
        background-color: #000;
      }
    }

    span {
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      border-radius: 100%;
      display: block;
      width: 12px;
      height: 12px;
      display: flex;
      align-items: center;

      &::before {
        content: "";
        position: absolute;
        left: 50%;
        top: 50%;
        border-radius: 100%;
        width: 20px;
        height: 20px;
        transform: translate(-50%, -50%);
        border: 2px solid #000;
      }

      .shipping-method-name {
        margin-left: 40px;
      }
    }

    img {
      padding-left: 40px;
    }

    i {
      padding-left: 25px;
      font-style: normal;
    }
  }

  &__label + &__label {
    @media (max-width: 575px) {
      margin-top: 30px;
    }

    @media (min-width: 576px) {
      margin-left: 60px;
    }
  }

  &__select {
    margin-top: 50px;
  }

  &__address {
    margin-top: 40px;
  }

  &__comment {
    margin-top: 60px;

    input {
      margin-top: 50px;
    }
  }

  &__btn {
    margin-top: 50px;

    .btn {
      text-transform: none;
      font-size: 20px;
      line-height: 24px;

      @media (min-width: 768px) {
        min-width: 440px;
      }

      @media (max-width: 767px) {
        width: 100%;
      }
    }
  }

  &__all {
    background: $color-grey;
    border-radius: 20px;
    padding: 35px;
  }

  &-top {
    display: flex;
    align-items: center;

    &__back {
      position: relative;
      font-size: 17px;
      color: #000;
      padding: 0 20px 0 15px;

      &:hover {
        text-decoration: underline;
      }

      &::before {
        content: '';
        background-image: url('../img/arrow-left-1.svg');
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        position: absolute;
        top: 3px;
        left: 0;
        width: 8px;
        height: 14px;
      }
    }

    &__total {
      font-size: 20px;
      font-weight: 700;
      color: $color-orange;

      @media (min-width: 576px) {
        font-size: 26px;
      }
    }
  }

  &-body {
    margin-top: 20px;

    &__item {
      padding-bottom: 20px;
      border-bottom: 2px solid #E5E5E5;
    }

    &__item + &__item {
      margin-top: 20px;
    }

    &__top {
      display: flex;
      align-items: center;

      @media (max-width: 480px) {
        flex-direction: column;
        align-items: flex-start;
      }

      p {
        font-size: 20px;
        font-weight: 700;

        @media (min-width: 576px) {
          font-size: 26px;
        }
      }

      span {
        color: #4A4A4A;
        margin-left: 30px;

        @media (max-width: 480px) {
          margin: 10px 0 0 0;
        }
      }
    }

    &__bottom {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-top: 10px;

      @media (max-width: 340px) {
        flex-direction: column;
        align-items: flex-start;

        p + p {
          margin-top: 10px;
        }
      }

      span {
        font-size: 16px;

        @media (min-width: 576px) {
          font-size: 21px;
        }
      }

      i {
        font-style: normal;
      }
    }
  }

  &-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 30px;

    p {
      font-size: 20px;
      font-weight: 700;

      @media (min-width: 576px) {
        font-size: 26px;
      }
    }

    span {
      color: $color-orange;
    }

    i {
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      color: $color-orange;
    }
  }

  &__confirm {
    margin-top: 20px;
    text-align: center;

    .btn {
      font-size: 20px;
      line-height: 24px;
      text-transform: none;
      width: 100%;

      @media (min-width: 576px) {
        max-width: 370px;
      }
    }
  }
}

.sublist{
  .order__title{
    text-transform: none;
    font-size: 20px;
  }
}

@media print { /*для печати*/

  .btn,
  .menu__icon,
  .menu__search-icon,
  .footer,
  .order-top__back,
  .order__close {
    display: none;
  }

  .container,
  .order__right,
  .booking__total,
  .order-booking__total {
    width: 100% !important;
    max-width: 100% !important;
  }

  .order-booking,
  .order__all {
    page-break-before: always;
  }

  .order__item {
    flex-direction: row !important;
    justify-content: space-between !important;
  }

  .order__all {
    padding: 0;
  }

  .order-booking .order__holder {
    max-width: 100%;
    padding-left: 30px;
    flex-direction: column !important;
  }


  .order-booking__total {
    justify-content: space-between;
  }
}